import React from 'react';
import Helmet from 'react-helmet';
import { Box } from '@chakra-ui/react';
import { default as SidebarContent } from './Sidebar';
import SidebarContainer from './../SidebarContainer';
import Main from './Main';
import Nav from './Nav';

const Page = ({
  title,
  header,
  subheader,
  metadata,
  children,
  titleLinkHandler,
}) => {
  return (
    <>
      {title && <Helmet title={`${title} | ${metadata.title}`} />}
      <SidebarContainer
        className="linear-bg"
        width={280}
        sidebar={
          <Box mt={[0, 0, 'auto']} mx={['auto', 'auto', 0]} p={'1rem'}>
            <SidebarContent
              metadata={metadata}
              titleLinkHandler={titleLinkHandler}
            />
          </Box>
        }
      >
        <>
          <div class="container">
            <Nav/>
            <Main
              metadata={metadata}
              header={header}
              subheader={subheader}
              children={children}
            />
          </div>
          <Box
            sx={{
              '@media print': {
                display: 'none',
              },
            }}
            py={'2.5rem'}
            textAlign="center"
            css={`
              background: ${(props) => props.theme.light.colors.secondary};

              @media (prefers-color-scheme: dark) {
                background: ${(props) => props.theme.dark.colors.secondary};
              }
            `}
          >
            &copy; {metadata.year} by {metadata.author.name}
            . All rights reserved. <br />
          </Box>
        </>
      </SidebarContainer>
    </>
  );
};

export default Page;
